.header {
  position: fixed; 
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  transition: background 0.1s ease 0s;
  background-color: #7965ad;
}

@media (max-width:$tablet-width) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    height: 92px;
  }

  .header--opened {
    height: 100vh;
    overflow-y: scroll;
  }
}

@media (max-width: $mobile-width) {
  .header {
    position: absolute;
    background: 0 0;
  }
}

.header__container { 
  max-width: 1920px;
  display: flex;
  align-items: center;
  padding-left: 40px;
  padding-right: calc((80 / 1920) * 100%);
  margin: 0 auto;
}

@media (max-width: 1279px) {
  .header__container {
    padding-right: 30px;
  }
}

@media (max-width: $tablet-width) {
  .header__container {
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;
  }
}

@media (max-width: $mobile-width) {
  .header__container {
    padding-top: 22px;
    padding-left: 17px;
    padding-right: 17px;
  }
}

