.header__btn { 
  padding: 0;
  background: 0 0;
  border: 0;
  cursor: pointer;
  outline: 0;
  color: inherit;
  display: none;
  position: absolute;
  right: 49px;
  top: 40px;
  width: 28px;
  height: 28px;
  overflow: hidden;
  transition: opacity 0.3s ease 0s;
  z-index: 1;
}

.header__btn:active {
  opacity: 0.4;
}

