}

.header__btn span {
  width: 19px;
  height: 3px;
  background: $basic-white;
  position: absolute;
  right: 0;
  border-radius: 3px;
  transition: background 0.3s ease 0s, transform 0.3s ease 0s;
}

.header__btn span::before {
  position: absolute;
  content: "";
  top: -7px;
  right: 0;
  width: 28px;
  height: 3px;
  background-color: $basic-white;
  border-radius: 3px;
}

.header__btn span::after {
  position: absolute;
  content: "";
  right: 0;
  bottom: -7px;
  width: 28px;
  height: 3px;
  background-color: $basic-white;
  border-radius: 3px;
}