@font-face {
  font-family: "lato";
  font-display: swap;
  src: url("../fonts/lato-v20-latin-700.woff") format("woff"), url("../fonts/lato-v20-latin-700.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "lato";
  font-display: swap;
  src: url("../fonts/lato-v20-latin-regular.woff") format("woff"), url("../fonts/lato-v20-latin-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "playfair-display";
  font-display: swap;
  src: url("../fonts/playfair-display-v25-cyrillic-900.woff") format("woff"), url("../fonts/playfair-display-v25-cyrillic-900.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: "playfair-display";
  font-display: swap;
  src: url("../fonts/playfair-display-v25-cyrillic-700.woff") format("woff"), url("../fonts/playfair-display-v25-cyrillic-700.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html,
body {
  box-sizing: border-box;
  height: 100%;
  font-size: 16px;
  line-height: 1;
}

body {
  font-family: "lato", "Arial", sans-serif;
  font-weight: 400;
  width: 100%;
  font-size: 18px;
  line-height: 150%;
  color: #303030;
  min-width: 320px;
  width: 100%;
  overflow: auto;
  box-sizing: inherit;
  color: #303030;
}

body._lock {
  overflow: hidden;
}

.body-wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(50%);
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  transition: background 0.1s ease 0s;
  background-color: #7965ad;
}

.header__container {
  max-width: 1920px;
  display: flex;
  align-items: center;
  padding-left: 40px;
  padding-right: 4.1666666667%;
  margin: 0 auto;
}

.header__title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: auto;
}

.header__logo {
  width: 260px;
  height: 145px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__hashtag {
  display: block;
  opacity: 0;
  transform: translate(-17px, 16px);
}

.header__wrapper {
  margin-left: 20px;
  width: 903px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.nojs .header__wrapper {
  position: static;
}

.header__main-nav {
  width: 82%;
  padding-top: 6px;
  margin-right: 12%;
}

.header__social-list {
  display: none;
}

.header__lang {
  order: 0;
  width: 90px;
  margin: 44px 92px 50px auto;
}

.header__btn {
  padding: 0;
  background: 0 0;
  border: 0;
  cursor: pointer;
  outline: 0;
  color: inherit;
  display: none;
  position: absolute;
  right: 49px;
  top: 40px;
  width: 28px;
  height: 28px;
  overflow: hidden;
  transition: opacity 0.3s ease 0s;
  z-index: 1;
}

.header__btn:active {
  opacity: 0.4;
}

.nojs .header__btn {
  display: none;
}

.header__btn span {
  width: 19px;
  height: 3px;
  background: #fff;
  position: absolute;
  right: 0;
  border-radius: 3px;
  transition: background 0.3s ease 0s, transform 0.3s ease 0s;
}

.header__btn span::before {
  position: absolute;
  content: "";
  top: -7px;
  right: 0;
  width: 28px;
  height: 3px;
  background-color: #fff;
  border-radius: 3px;
}

.header__btn span::after {
  position: absolute;
  content: "";
  right: 0;
  bottom: -7px;
  width: 28px;
  height: 3px;
  background-color: #fff;
  border-radius: 3px;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.main-nav__item {
  position: relative;
  margin-right: 7.1982281285%;
}

.main-nav__item:last-child {
  margin-right: 0;
}

.main-nav__link {
  margin: 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  font-weight: 700;
  font-style: normal;
  line-height: 22px;
  color: #fff;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  position: relative;
  padding: 0 16px 4px;
  z-index: 1;
  transition: color 0.3s ease;
}

.main-nav__link::after {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 16px;
  top: 10px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  transition: background 0.3s ease;
}

.lang__title {
  font-size: 16px;
  font-weight: 600;
  font-style: normal;
  line-height: 19px;
  color: #fff;
  letter-spacing: -0.02em;
  font-family: "lato", "Arial", sans-serif;
  text-transform: uppercase;
  opacity: 0.64;
  background-color: transparent;
}

@media (max-width: 1439px) {
  .main-nav__item {
    margin-right: 2%;
  }
}

@media (max-width: 1279px) {
  .header__container {
    padding-right: 30px;
  }

  .main-nav__item {
    margin-right: 1%;
  }
}

@media (max-width: 1023px) {
  html {
    scroll-behavior: auto;
  }

  .header {
    position: fixed;
    top: 0;
    left: 0;
    height: 92px;
  }

  .header--opened {
    height: 100vh;
    overflow-y: scroll;
  }

  .header__container {
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;
  }

  .main-nav__item {
    margin-right: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.16);
  }

  .main-nav__item:last-child {
    border: 0;
  }

  .main-nav__link {
    font-family: "playfair-display", "Arial", sans-serif;
    font-size: 56px;
    font-weight: 900;
    font-style: normal;
    line-height: 62px;
    color: rgba(255, 255, 255, 0.87);
    letter-spacing: 0.03em;
    text-transform: none;
    padding-top: 46px;
    padding-bottom: 39px;
  }

  .main-nav__link::after {
    display: none;
  }
}

@media (max-width: 767px) {
  .header {
    position: absolute;
    background: 0 0;
  }

  .header__container {
    padding-top: 22px;
    padding-left: 17px;
    padding-right: 17px;
  }

  .main-nav__item {
    margin-right: 0;
  }

  .main-nav__link {
    font-size: 32px;
    font-weight: 700;
    font-style: normal;
    line-height: 35px;
    color: rgba(255, 255, 255, 0.87);
    padding-top: 28px;
    padding-bottom: 23px;
  }
}